























































export default {
	data() {
		return {
			address:{},
			goods:{},
			goods_num:'',
			ordernum:'',
			transportprice:''
		};
	},
	onLoad:function(e){
		var token=(uni.getStorageSync("user_info")).token;
		var oreder_num=uni.getStorageSync("oreder_num");
		uni.request({
			url: 'http://bus.liebianzhe.com/api/order/do_order',
			method: 'POST',
			data: {
				token:token,
				ordernum:oreder_num
			},
			success: res => {
				if(res.data.code==1){
					console.info(res.data);
					this.goods=res.data.data.goods;
					this.goods_num=res.data.data.goods_num;
					this.ordernum=res.data.data.ordernum;
					this.transportprice=res.data.data.transportprice;
					if(e.address_type==0){
						this.address=res.data.data.address
					}
				}else if(res.data.code==10){
					uni.removeStorage({
						key:'user_info',
					})
					uni.showToast({
						title: res.data.msg,
						icon:'none',
						mask: false,
						duration: 1000
					});
					setTimeout(function(){
						uni.reLaunch({
							url:'../../login/login'
						})
					},1500)
					
				}else{
					uni.showToast({
						title: res.data.msg,
						icon:'none',
						mask: false,
						duration: 1000
					});
				}
			},
			fail: () => {
				console.info("请求失败~");
			},
		});
	},
	methods:{
		reduce(){
			this.goods_num==1?this.goods_num=1:this.goods_num=parseInt(this.goods_num)-1
		},
		add(){
			this.goods_num=parseInt(this.goods_num)+1;
		},
		my_address(){
			uni.navigateTo({
				url: '../my-address/my-address',
			});
		}
	}
}
